	module.exports = {"pathContext":{"frontmatter":{"path":"/docs/align-items","title":"Align Items / Self","hasPlayground":true,"initialPlayground":null,"redirect":null},"html":"<h2>Align Items</h2>\n<p>Align items describes how to align children along the cross axis of their container.\nAlign items is very similar to <a href=\"/yoga/docs/justify-content\"><code class=\"language-text\">justify content</code></a> but instead of\napplying to the main axis, <code class=\"language-text\">align items</code> applies to the cross axis.</p>\n<p><strong>STRETCH (DEFAULT)</strong> Stretch children of a container to match the <code class=\"language-text\">height</code> of the container's cross axis.</p>\n<p><strong>FLEX START</strong> Align children of a container to the start of the container's cross axis.</p>\n<p><strong>FLEX END</strong> Align children of a container to the end of the container's cross axis.</p>\n<p><strong>CENTER</strong> Align children of a container in the center of the container's cross axis.</p>\n<p><controls prop=\"alignItems\"></controls></p>\n<h2>Align Self</h2>\n<p>Align self has the same options and effect as <code class=\"language-text\">align items</code> but instead of\naffecting the children within a container, you can apply this property to\na single child to change its alignment within its parent. <code class=\"language-text\">align self</code>\noverrides any option set by the parent with <code class=\"language-text\">align items</code>.</p>\n<p><controls prop=\"alignSelf\"></controls></p>"}}

